.External_Links-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 60px;
  margin-bottom: 24px;
}

.External_Links-container_group-1,
.External_Links-container_group-2 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  a {
    text-decoration: none;
    color: #8e8e8e;
    padding: 3px 8px;

    p {
      font-size: 14px;
      font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
        sans-serif;
    }
  }
}

.External_Links-container_group-3 {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
  padding-bottom: 15px;

  select {
    color: #8e8e8e;
    border: none;
    background-color: #fafafa;
  }

  p {
    color: #8e8e8e;
    font-size: 13px;
  }
}
